<template>
  <section class="video">
    <div class="inner">
      <div class="example-modal-window">
        <p>     .</p>
        <button @click="isShow('true')" class="btn btn-round btn-round--xlg"></button>
        <!--  MyModal -->
      </div>
    </div>
  </section>
</template>

<script>
import * as eventBus from "@/eventBus.js";

export default {
  methods: {
    isShow(_isShow) {
      eventBus.player.$emit("isShow", _isShow);
    },
  },
};
</script>
