<template>
  <div id="wrap" data-target-device="pc">
    <Header></Header>
    <transition name="container">
      <router-view />
    </transition>
    <Footer></Footer>
    <Player></Player>
    <PopupTerms></PopupTerms>
    <PopupPersonal></PopupPersonal>
    <PopuprRefusal></PopuprRefusal>
  </div>
</template>

<script>
import Header from "./components/layout/Header";
import Footer from "./components/layout/Footer";
import Player from "./components/modal/Player.vue";
import PopupTerms from "./views/common/PopupTerms.vue";
import PopupPersonal from "./views/common/PopupPersonal.vue";
import PopuprRefusal from "./views/common/PopuprRefusal.vue";

export default {
  name: "wrap",
  components: {
    Header,
    Footer,
    Player,
    PopupTerms,
    PopupPersonal,
    PopuprRefusal,
  },
  data() {
    return {
      //
    };
  },
  watch: {
    $route() {
      //
    },
  },
  methods: {
    /*     */
    onCommonJs() {
      (function () {
        /**
         *
         *  Device  (custom data attributes)
         *
         */

        let $wrap = $("#wrap");
        let detail = $.getDevice().detail;

        $wrap.wait(100).attr("data-device-detail", detail);
      })();
      /*
      
      */
      (function () {
        /**
         *
         *       
         *
         *   [URL  ]
         *   https:///?moveto=data-card= // $("[data-card='']")  
         *   https:///?moveto=section // $("#section")  
         *   https:///?moveto=card // $(".card")  
         *   https:///?moveto=card--.js-sticky // $(".js-sticky")   $(".card")  
         *   https:///?moveto=card--50 // 50px   $(".card")  
         *
         */
        setTimeout(() => {
          $.moveToParams();
        }, 1000);
      })();
      /*
      
      */
      (function () {
        /**
         *
         * () 
         *
         */

        $.disabledDoubleTab();
      })();
      /*
      
      */
      (function () {
        /**
         *
         * .SVG to XML 
         *
         */
        $('img[src*=".svg"]').makeSvg();
      })();
      /*
      
      */
      (function () {
        /**
         *
         * wrapper is ready
         *
         */
        let $wrap = $("#wrap");
        let $loding = $("#lodingWrap");

        $loding.remove();
        $wrap.css({
          opacity: 1,
          visibility: "visible",
        });
      })();
      /*
      
      */
      (function () {
        /**
         *
         *  
         *
         */
        $(".js-anchor").anchor();
      })();
      /*
      
      */
      (function () {
        /**
         *
         * form    
         *
         */
        let $wrap = $("#wrap");

        $wrap.formTamplate();
      })();
      /*
      
      */
      (function () {
        /**
         *
         *.js-is-appear FadeIn  
         *
         *  [html  ]
         *      // 
         *      <div class="js-is-appear">
         *
         *      //scroll.top  (: class   )
         *      <div class="js-is-appear50">
         *
         *  [CSS  ]
         *      div {opacity:0;transition:none}
         *      div.appear {opacity:1;transition:opacity 0.3s ease-in-out}
         *
         */
        const $appears = document.querySelectorAll("[class*=js-is-appear]");

        $appears.forEach((_each) => {
          const $this = _each;
          const classNames = $this.getAttribute("class").split(" ");
          const posY = classNames
            .map((_arr) => {
              if (_arr.indexOf("js-is-appear") !== -1) {
                let num = Number(_arr.replace(/[^0-9]/g, ""));

                $this.classList.remove(_arr); //removeClass
                return (num = num !== 0 ? num : 70); //scroll.top  70 
              }
            })
            .filter((_el) => _el)[0];

          // addClass('js-is-appear')
          $.scrollAction({
            $target: $this,
            top: posY,
            scrollDownAction: function () {
              $this.classList.add("is-appear");
            },
            scrollUpAction: function () {
              $this.classList.remove("is-appear");
            },
          });
        });
      })();
      /*
      
      */
      (function () {
        /**
         *
         * TEXT 
         *
         */
        $(".js-clamp-1").clamp(1);
        $(".js-clamp-2").clamp(2);
        $(".js-clamp-3").clamp(3);
      })();
      /*
      
      */
      (function () {
        /**
         *
         *     
         *
         * [html  ]
         * <div data-loding-spinner="true"></div> //  
         * <div data-loding-spinner="false"></div> //  
         * <div data-loding-spinner="true dimmed"></div> //        ( : )
         * <div data-loding-spinner="true 1rem"></div> // rem      ( : 6rem)
         *
         */

        $("[data-loding-spinner]").lodingSpinner();
      })();
      /*
      
      */
      (function () {
        /**
         *
         *    
         *
         */

        let winH = window.innerHeight;
        let wrapH = $("#wrap").outerHeight();
        let $container = $("#container");
        let $btnTop;

        // windows   documnet 1.5  "TOP Button" Node 
        if (winH * 1.5 < wrapH) {
          $btnTop = $(`
            <aside id="aisdeBtnTop">
                <button class="btn btn-top js-anchor" data-anchor="#wrap" type="button"><span>TOP</span></button>
            </aside>`);

          $container.append($btnTop); //  
          isActive(); //  , 
          $btnTop.find(".js-anchor").anchor(); //    
        }

        // windows 1/3    "TOP Button" -
        let status;
        function isActive() {
          $(window).on("scroll", function () {
            let winT = $(this).scrollTop();

            if (winH / 3 < winT) {
              if (!status) $btnTop.addClass("is-active");
              status = true;
            } else {
              if (status) $btnTop.removeClass("is-active");
              status = false;
            }
          });
        }

        // 3     "TOP Button" 
        setInterval(function () {
          if (status) {
            $btnTop.removeClass("is-active");
            status = false;
          }
        }, 3000);
      })();
      /*
      
      */
    },
  },
  created() {
    //
  },
  mounted() {
    this.onCommonJs();
  },
  updated() {
    this.onCommonJs();
  },
};
</script>

<style lang="scss" scoped>
.container {
  &-enter-active,
  &-leave-active {
    transition-property: opacity;
    transition-duration: #{$G-container-delay + "s"};
  }
  &-enter-active {
    transition-delay: #{$G-container-delay + "s"};
  }
  &-enter,
  &-leave-active {
    opacity: 0;
  }
}
</style>
